'use strict';

var Slider = require('./../pages/slider');
var ChampionSignup = require('./signup');

var Home = function () {
    'use strict';

    var load = function load() {
        Slider.init();
        var hash = window.location.hash.substring(1);
        if (hash === 'signup') {
            ChampionSignup.showModal();
        }
    };

    var unload = function unload() {
        Slider.destroy();
    };

    return {
        load: load,
        unload: unload
    };
}();

module.exports = Home;


